function Map({setLocation, position, lat, lng}) {
    // const [location, setLocation]  = useState({});
    const toCenter = useRef(false);
    // console.log(lat, lng, "    1")
    console.log(position,"------lll")
    // console.log(location,"----------ui")
    // console.log(events)
    // products ? [products?.position[0]?.lat, products?.position[0]?.lng] : null
    // console.log(position,"map page")
    const [map, setMap] = useState(null)
    useEffect(()=>{
        if(map){
            map.flyTo([position.lat, position.lng])
        }
    }, [position?.lat])
    const displayMap = useMemo(
        () => (
            <MapContainer 
                center={[position?.lat, position?.lng]} 
                // center={position?.lat ? [position?.lat, position?.lng] : [props_lat, props_lng]} 
                zoom={15} scrollWheelZoom={true}
                whenCreated = {setMap}
                style={{height:'100%'}}
                >
                    {/* <ChangeView center = {[position?.lat, position?.lng]} zoom = {15}/> */}
                    
                    <LocationIcon className="absolute text-gray-800 top-1/2 left-1/2 text-5xl -mt-11 -ml-6" style={{zIndex:100000}}
                    
                    />

                    <TileLayer

                        url="https://gamysh.com/map/tile/{z}/{x}/{y}.png"
                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    />
                {/* <Marker 
                position={[37.92227, 58.37601]}
                >
                <Popup>
                    your home location
                    </Popup>
                </Marker> */}
                    {/* <SetViewOnClick toCenter={toCenter} setLocation={setLocation}/> */}
                    <MapComponent setLocation={setLocation} toCenter={toCenter} />
            </MapContainer>
        ), 
    [])
    return (
        <div className="relative h-full">
            {displayMap}
        </div>
    );
}

export default Map;